import React from 'react';
import './TeamTwoList.css';

function TeamTwoList (props) {

    const teamTwoPlayers = props.value;
    const teamTwoListOfPlayers = teamTwoPlayers.map( 
        (teamTwoPlayer) => 
        <li key={teamTwoPlayer.toString()}>
            {teamTwoPlayer}
        </li>
    )

return(

    <ul className="teamtwolistbackground">
        {teamTwoListOfPlayers}
    </ul>

)


}

export default TeamTwoList;