import React from 'react';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import PlayerPool from '../PlayerPool/PlayerPool';
import GenerateBtn from '../GenerateBtn/GenerateBtn';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import './AddPlayerField.css';
// import { createPortal } from 'react-dom';
// import TeamOne from '../TeamOne/TeamOne';
// import TeamTwo from '../TeamTwo/TeamTwo';
// import FreeAgent from '../FreeAgent/FreeAgent';


class AddPlayerField extends React.Component {

  // set up state for added names
  constructor (props){
    super(props);
    this.state = {

      playerAdded: '',
      playersArray: [],
      randomPlayersArray: [],
      teamOneArray: [],
      teamTwoArray: [],
      singlePlayer: [],
      displayColOne: true,
      displayColTwo: false,
      displayColThree: false,
      displayColFour: false,
      displayColFive: false,
      displayColSix: false,
      displayLimitReached: false,
      disableEntries: false,
      colTeamOneDisplay: [],
      colTeamTwoDisplay: [],
      colTeamThreeDisplay: [],
      colTeamFourDisplay: [],
      colTeamFiveDisplay: [],
      colTeamSixDisplay: [],
      displayAll: false,
    };
  
    this.handleInputChange = this.handleInputChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
    this.listOfPlayers = this.listOfPlayers.bind(this);
    this.fisherMix = this.fisherMix.bind(this);
  
  };

// set up an onlick for when name is entered and then is added to players array
listOfPlayers = (event) => {
  
  this.setState( state => {

  // create a copy of playersArray 
  const addingPlayersToPool = [...this.state.playersArray];

  // going to concat players from form to the copy of playersArray

  let updatePlayersArray = addingPlayersToPool.concat(state.playerAdded);

   
// going to use Set data object to remove duplicate names from array
    const removeDupFromArray = new Set (updatePlayersArray);


// Set up responsive cols/views - if more than 5 names are added then array splits 
  // let playersArray = [...removeDupFromArray];

  let colSortArray = [...removeDupFromArray];

   let colTeamOneDis = [...this.state.colTeamOneDisplay];
   let colTeamTwoDis = [...this.state.colTeamTwoDisplay];
   let colTeamThreeDis = [...this.state.colTeamThreeDisplay];
   let colTeamFourDis =[...this.state.colTeamFourDisplay];
   let colTeamFiveDis =[...this.state.colTeamFiveDisplay];
   let colTeamSixDis =[...this.state.colTeamSixDisplay];

    if (colSortArray.length <= 4){

      colTeamOneDis = colSortArray;

    } 
    else if (4 < colSortArray.length && colSortArray.length <= 8 ){
 
      colTeamOneDis = colSortArray.slice(0, 4);
      
      colTeamTwoDis = colSortArray.slice(4, 8);

      this.setState({

        displayColTwo: true,

      });
   }
   else if (8 < colSortArray.length && colSortArray.length <= 12){

    colTeamOneDis = colSortArray.slice(0,4);
      
    colTeamTwoDis = colSortArray.slice(4,8);

    colTeamThreeDis = colSortArray.slice(8, 12);

    this.setState({
      displayColTwo: true,
      displayColThree: true,
    });

    // this.setState({
    //   displayColThree: true,
    // });
  }
   else if (12 < colSortArray.length && colSortArray.length <= 16){

    colTeamOneDis = colSortArray.slice(0, 4);
      
    colTeamTwoDis = colSortArray.slice(4, 8);

    colTeamThreeDis = colSortArray.slice(8, 12);

    colTeamFourDis = colSortArray.slice(12, 16);

    this.setState({
      displayColTwo: true,
      displayColThree: true,
      displayColFour: true,
    });
   }
   else if (16 < colSortArray.length && colSortArray.length <= 20){

    colTeamOneDis = colSortArray.slice(0, 4);
      
    colTeamTwoDis = colSortArray.slice(4, 8);

    colTeamThreeDis = colSortArray.slice(8, 12);

    colTeamFourDis = colSortArray.slice(12, 16);

    colTeamFiveDis = colSortArray.slice(16, 20);

    this.setState({
      displayColTwo: true,
      displayColThree: true,
      displayColFour: true,
      displayColFive: true,
    });
   } else if (20 < colSortArray.length && colSortArray.length <= 24 ){

    colTeamOneDis = colSortArray.slice(0, 4);  
    colTeamTwoDis = colSortArray.slice(4, 8);
    colTeamThreeDis = colSortArray.slice(8, 12);
    colTeamFourDis = colSortArray.slice(12, 16);
    colTeamFiveDis = colSortArray.slice(16, 20);
    colTeamSixDis = colSortArray.slice(20, 24);

    this.setState({
      displayColTwo: true,
      displayColThree: true,
      displayColFour: true,
      displayColFive: true,
      displayColSix: true,
    });

      if ( colSortArray.length === 24){

        this.setState({
          displayLimitReached: true,
          disableEntries: true
        })

      }
    
   } 
  //  else {

  
  //     this.setState({
  //       displayLimitReached: true,
  //       disableEntries: true,
  //     });

    

  //  }
   
  //  playersArray = colSortArray;
  
   let colTeamOneDisplay = [...colTeamOneDis];
   let colTeamTwoDisplay = [...colTeamTwoDis];
   let colTeamThreeDisplay = [...colTeamThreeDis];
   let colTeamFourDisplay = [...colTeamFourDis];
   let colTeamFiveDisplay = [...colTeamFiveDis];
   let colTeamSixDisplay = [...colTeamSixDis];
    
   let playersArray = colSortArray;

   this.setState({

      playersArray: colSortArray,

   })
    return{
      colTeamOneDisplay,
      colTeamTwoDisplay,
      colTeamThreeDisplay,
      colTeamFourDisplay,
      colTeamFiveDisplay,
      colTeamSixDisplay,
      playersArray,
      playerAdded: '',
    }
    
  });

  // console.log(this.state.playersArray)
};

// set up function to mix array and then console.log
fisherMix = () => {
  console.log("hello");

  this.setState({

    displayAll: true,

  })


 
  // create a copy of our player array(state has to be immutable) 
  const arrForRandom = [...this.state.playersArray];

  // use fisher yates shuffle on this array copy to shuffle elements
  for (let i = arrForRandom.length - 1; i > 0; i--) 
  {
    let j = Math.floor(Math.random() * (i + 1));
    let temp = arrForRandom[i];
    arrForRandom[i] = arrForRandom[j];
    arrForRandom[j] = temp;
    
    this.setState({

      randomPlayersArray: arrForRandom,

    });
  
  };
  // console.log(this.state.randomPlayersArray); 

// create a copy of shuffled array
  
  const createTeams = [...this.state.randomPlayersArray];

  // console.log(createTeams);
 
// split array into two new arrays and free agent array

  if (createTeams.length %2 !== 0){

    let removeOddPlayer = createTeams.pop();
    this.setState({
        singlePlayer: removeOddPlayer
    });

    console.log(this.state.singlePlayer);

    let dividePlayers = createTeams.length * 0.5;
      let firstTeam = createTeams.slice(0, dividePlayers);

      this.setState({

        teamOneArray: firstTeam,

      });

      console.log(this.state.teamOneArray);

      let secondTeam = createTeams.slice(dividePlayers);

      this.setState({

        teamTwoArray: secondTeam,

      });

      console.log(this.state.teamTwoArray);

  } else {
    let dividePlayers = createTeams.length * 0.5;

    let firstTeam = createTeams.slice(0, dividePlayers);

    this.setState({

      teamOneArray: firstTeam,

    });

    console.log(this.state.teamOneArray);

    let secondTeam = createTeams.slice(dividePlayers);

    this.setState({

      teamTwoArray: secondTeam,

    });

    console.log(this.state.teamTwoArray);

    this.setState({
      singlePlayer: [],
  });

  }

}
      
// updates playerAdded state with the name added from input
 handleInputChange = event => {

    let value = event.target.value.toUpperCase();

    if ( value !== ''){
      this.setState({
        playerAdded: value
      });
    } else if (value === '' ){
      this.setState({
        playerAdded: ''
      })
    }

    
  }

// prevent page from refreshing 
handleSubmit = event =>{
  event.preventDefault();
  this.listOfPlayers();
 
}


  render(){
    return(

      <div>

      <Container > 
          <Row className="justify-content-md-center stylethecontainer">
            <Col xs={9} md={6} lg={4} className="colresone">
            
            <Form onSubmit={this.handleSubmit}>
              <Form.Group className="mb-3" controlId="exampleForm.ControlInput1">
                <Form.Control 
                size="lg"
                type="text" 
                placeholder="PLAYER NAME"
                value={this.state.playerAdded}
                onChange={this.handleInputChange}
                disabled={this.state.disableEntries}
               
                />
              </Form.Group>
            </Form>

             {this.state.displayLimitReached &&
              <Form.Text    className="text-muted">
               player pool limit reached
              </Form.Text>
               }
            </Col>
            <Col xs={3} md={3} lg={2} className="colrestwo">
                <Button
                className="btnstyling" 
                
                onClick={this.listOfPlayers}
                disabled={this.state.disableEntries}
                
                >ADD</Button>
            </Col>
          </Row>
         
          <Row className="text-center">
            
                <PlayerPool 
                value={this.state.colTeamOneDisplay}
                colDisplay={this.state.displayColOne}

                displayTeamTwo={this.state.colTeamTwoDisplay}
                colDisplayTwo={this.state.displayColTwo}

                displayTeamThree = {this.state.colTeamThreeDisplay}
                colDisplayThree={this.state.displayColThree}

                displayTeamFour = {this.state.colTeamFourDisplay}
                colDisplayFour={this.state.displayColFour}

                displayTeamFive = {this.state.colTeamFiveDisplay}
                colDisplayFive={this.state.displayColFive}
            
                displayTeamSix = {this.state.colTeamSixDisplay}
                colDisplaySix={this.state.displayColSix}

                />    
        
          </Row>

          <Row>
            <Col
            className="text-center">
                
              <GenerateBtn 
              value={this.fisherMix}
              first={this.state.teamOneArray}
              second={this.state.teamTwoArray}
              lone={this.state.singlePlayer}
              showTeams={this.state.displayAll}/>
              
              
              {/* <TeamOne value={this.state.teamOneArray}/>
              <TeamTwo value={this.state.teamTwoArray}/>
              <FreeAgent value={this.state.singlePlayer}/> */}
            
            </Col>
          </Row>



       

       </Container>
       </div>



    )

    }


}

export default AddPlayerField;


