import React from 'react';
import './TeamOneList.css';

function TeamOneList (props) {

    const teamOnePlayers = props.value;
    const teamOneListOfPlayers = teamOnePlayers.map( 
        (teamOnePlayer) => 
        <li key={teamOnePlayer.toString()}>
            {teamOnePlayer}
        </li>
    )

return(

    <ul className="teamonelistbackground">

        {teamOneListOfPlayers}
    </ul>

)


}

export default TeamOneList;